export const categoryConfig = {
  'Gastronomie': { emoji: '', color: '#FF5733' },
  'Kavrny a pekrny': { emoji: '', color: '#8E44AD' },
  'Bary a non ivot': { emoji: '', color: '#E74C3C' },
  'Krsa a viz': { emoji: '', color: '#E91E63' },
  'Wellness a relaxace': { emoji: '', color: '#00BCD4' },
  'Sport a fitness': { emoji: '', color: '#16A085' },
  'Zdrav a medicna': { emoji: '', color: '#C0392B' },
  'Obchody a kvtinstv': { emoji: '', color: '#34495E' },
  'Ubytovn': { emoji: '', color: '#2980B9' },
  'Kultura a umn': { emoji: '', color: '#9C27B0' },
  'Sluby a ostatn': { emoji: '', color: '#94a3b8' },
};

export const getCategoryStyle = (category) => {
  if (!category) return categoryConfig['Sluby a ostatn'];
  
  // Pokud je to u agregovan kategorie
  if (categoryConfig[category]) {
    return categoryConfig[category];
  }

  // Fallback pro star voln s neagregovanou kategori
  return categoryConfig['Sluby a ostatn'];
};
